<template>
  <div class="home">
    <main class="main">
      <figure class="main__figure">
        <SpinnerComponent v-if="loading"></SpinnerComponent>
        <img :src="mainItem.image" :alt="mainItem.name">
        <img class="main__new" width="56" height="55" src="../assets/new_icon.jpg" alt="icon new">
      </figure>
      <section class="main__content">
        <SpinnerComponent v-if="loading"></SpinnerComponent>
        <h1 class="main__title">{{ mainItem.name }} {{ mainItem.brand }}</h1>
        <p class="main__text">{{ mainItem.description }}</p>
        <router-link type="button" :to="`/product/details/${mainItem.id}/${mainItem.name}/${mainItem.brand}`" class="main__btn">More</router-link>
      </section>
      <h2 class="main__price">{{ localePrices(mainItem.price) }}</h2>
      <div class="main__bg"></div>
      <div class="main__bg main__bg--2"></div>
      <!-- <img class="main__bg" src="../assets/svg/bg_wave_2.svg" alt="bg gradient wave 2"> -->
      <!-- <img class="main__bg main__bg--2" src="../assets/svg/bg_wave.svg" alt="bg gradient wave"> -->
    </main>
    <section class="container section">
      <h2 class="main-title">Our products</h2>
      <ul class="grid-list">
        <CardComponent :products="items"></CardComponent>
        <SpinnerComponent v-if="loading"></SpinnerComponent>
      </ul>
    </section>
  </div>
</template>

<script>
import { localePrices } from '../helpers/formatPrice'
import CardComponent from '@/components/CardComponent.vue';
import SpinnerComponent from '@/components/SpinnerComponent.vue';

  export default {
    name: 'HomeView',
    data(){
      return{
        items: [],
        mainItem: {},
        loading: false,
      }
    },
    components: {
      CardComponent,
      SpinnerComponent,
    },
    mounted(){
      this.getItems();
    },
    methods:{
      localePrices,
      async getItems(){
        try {
          this.loading = true;
          const response = await fetch('https://my-json-server.typicode.com/DavidXploidz/online_store/clothes?_page=1&_limit=10');
          const data = await response.json();
          this.items = data;
          this.mainItem = data[data.length - 1];
          this.loading = false;
        } catch (error) {
          console.log(error);
        }
      },
    }
  }
</script>

